<template>
    <div class="files">
        <div
            v-for="(file, fileIndex) in files"
            :key="fileIndex"
            class="file__images"
        >
            <div class="file__square">
                <i class="file__delete" @click="$refs.upload.remove(file)" />

                <img class="file__image" :src="file.blob" />
            </div>
        </div>

        <file-upload
            v-show="!isLimitReached"
            ref="upload"
            v-model="files"
            :maximum="maximum"
            :multiple="true"
            accept="image/*"
            @input-filter="inputFilter"
        >
            <span
                :class="[
                    'files__button',
                    { files__button_disabled: files.length >= maximum },
                ]"
            >
                <i class="files__icon" />

                <span class="files__text"> Прикрепить файл </span>
            </span>
        </file-upload>

        <span
            v-if="isLimitReached"
            class="files__button files__button_disabled"
        >
            <i class="files__icon" />

            <span class="files__text"> Прикрепить файл </span>
        </span>
    </div>
</template>

<script>
/* eslint-disable no-param-reassign */
/* eslint-disable no-plusplus */
/* eslint-disable consistent-return */
export default {
    data() {
        return {
            maximum: 5,
            files: [],
        };
    },

    computed: {
        isLimitReached() {
            return this.files.length >= this.maximum;
        },
    },

    watch: {
        files(files) {
            this.$emit('fileAdded', files);
        },
    },

    methods: {
        inputFilter(newFile, oldFile, prevent) {
            if (newFile && !oldFile) {
                // Add file

                // Filter non-image file
                // Will not be added to files
                if (!/\.(jpeg|jpe|jpg|gif|png|webp)$/i.test(newFile.name)) {
                    return prevent();
                }

                // Create the 'blob' field for thumbnail preview
                newFile.blob = '';
                const URL = window.URL || window.webkitURL;
                if (URL && URL.createObjectURL) {
                    newFile.blob = URL.createObjectURL(newFile.file);
                }
            }

            if (newFile && oldFile) {
                // Update file

                // Increase the version number
                if (!newFile.version) {
                    newFile.version = 0;
                }
                newFile.version++;
            }

            if (!newFile && oldFile) {
                // Remove file
                // Refused to remove the file
                // return prevent()
            }
        },
    },
};
</script>

