<template>
    <v-input-container class="input">
        <span
            v-if="icon"
            :class="['input__icon', `input__icon_image_${icon}`]"
        />

        <input
            autocomplete="off"
            class="input__field"
            :name="name"
            :value="value"
            :type="type"
            :placeholder="placeholder"
            :maxlength="maxlength"
            :minlength="minlength"
            :max="max"
            :min="min"
            @input="$emit('input', $event.target.value)"
            @blur="$emit('blur', $event.target.value)"
            @change="$emit('blur', $event.target.value)"
        />
    </v-input-container>
</template>

<script>
import VInputContainer from '@/components/ui/VInputContainer.vue';

export default {
    components: {
        VInputContainer,
    },

    props: {
        icon: {
            type: String,
            default: null,
        },

        value: {
            type: String,
            default: '',
        },

        type: {
            type: String,
            default: 'text',
            validator(value) {
                return [
                    'text',
                    'number',
                    'email',
                    'password',
                    'search',
                    'url',
                    'tel',
                ].includes(value);
            },
        },

        placeholder: {
            type: String,
            default: '',
        },

        name: {
            type: String,
            required: true,
        },

        max: {
            type: String,
            default: undefined,
        },

        min: {
            type: String,
            default: undefined,
        },

        maxlength: {
            type: String,
            default: undefined,
        },

        minlength: {
            type: String,
            default: undefined,
        },
    },
};
</script>

<style lang="css" scoped>
.input {
    align-items: flex-end;
}

.input__icon {
    width: 16px;
    height: 16px;
    filter: invert(100%);
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    margin-right: 12px;
}

.input__icon_image_email {
    background-image: url('~@/assets/images/icons/email.svg');
}

.input__field {
    width: 100%;
    color: inherit;
    margin: 0;
    padding: 0;
    border: none;
    background-color: transparent;
    font-family: inherit;
    outline: none;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: inherit;
}

.input__field::placeholder {
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #757575;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
    border: none;
    -webkit-text-fill-color: #fff;
    -webkit-box-shadow: unset;
    transition: background-color 5000s ease-in-out 0s;
}
</style>
