<template>
    <v-input-container class="input">
        <textarea
            class="input__field"
            :name="name"
            :value="value"
            :placeholder="placeholder"
            :maxlength="maxlength"
            :minlength="minlength"
            :rows="rows"
            @input="handleInputChange($event)"
        />
    </v-input-container>
</template>

<script>
import VInputContainer from '@/components/ui/VInputContainer.vue';

export default {
    components: {
        VInputContainer,
    },

    props: {
        value: {
            type: String,
            default: '',
        },

        placeholder: {
            type: String,
            default: '',
        },

        rows: {
            type: String,
            default: '4',
        },

        name: {
            type: String,
            required: true,
        },

        maxlength: {
            type: String,
            default: undefined,
        },

        minlength: {
            type: String,
            default: undefined,
        },
    },

    methods: {
        handleInputChange(event) {
            this.$emit('input', event.target.value);
        },
    },
};
</script>

