<template>
    <v-select
        v-model="selected"
        class="select"
        label="text"
        :options="options"
        :reduce="(option) => option.id"
        :clearable="false"
        :searchable="false"
        :components="{ OpenIndicator }"
        @option:selected="$emit('input', selected)"
    />
</template>

<script>
import 'vue-select/dist/vue-select.css';

export default {
    props: {
        value: {
            type: Number,
            required: true,
        },

        options: {
            type: Array,
            required: true,
        },
    },

    data() {
        return {
            selected: this.value,
            OpenIndicator: {
                render: (createElement) =>
                    createElement('span', { class: { select__open: true } }),
            },
        };
    },
};
</script>

