<template>
    <v-card class="feedback">
        <ValidationObserver v-slot="{ invalid }" slim>
            <form
                class="feedback__form"
                novalidate
                @submit.prevent="handleSubmitForm"
            >
                <v-custom-select
                    v-model="feedback"
                    class="feedback__select"
                    :options="feedbackOptions"
                />

                <ValidationProvider
                    v-slot="{ errors }"
                    rules="required|email"
                    slim
                    mode="eager"
                >
                    <div class="feedback__input">
                        <v-input
                            v-model.trim="email"
                            icon="email"
                            type="email"
                            name="email"
                            placeholder="Ваш email"
                            :class="[
                                'feedback__email',
                                { feedback__email_invalid: errors[0] },
                            ]"
                            maxlength="256"
                        />

                        <v-tooltip-error
                            v-if="errors[0]"
                            class="feedback__error"
                        >
                            Введите корректный email
                        </v-tooltip-error>
                    </div>
                </ValidationProvider>

                <ValidationProvider rules="required" slim>
                    <v-text-area
                        v-model.trim="message"
                        class="feedback__textarea"
                        name="message"
                        placeholder="Текст"
                        maxlength="2048"
                        rows="13"
                    />
                </ValidationProvider>

                <div class="feedback__footer">
                    <v-file-uploader
                        class="feedback__file-uploader"
                        @fileAdded="handeFileAdded"
                    />

                    <v-button class="feedback__submit" :disabled="invalid">
                        Отправить
                    </v-button>
                </div>
            </form>
        </ValidationObserver>
    </v-card>
</template>

<script>
import VCard from '@/components/VCard.vue';
import VInput from '@/components/ui/VInput.vue';
import VTextArea from '@/components/ui/VTextArea.vue';
import VCustomSelect from '@/components/ui/VCustomSelect.vue';
import VButton from '@/components/ui/VButton.vue';
import VFileUploader from '@/components/ui/VFileUploader.vue';
import VTooltipError from '@/components/ui/VTooltipError.vue';

import modalActions from '@/mixins/modal-actions';

export default {
    components: {
        VCard,
        VInput,
        VTextArea,
        VCustomSelect,
        VButton,
        VFileUploader,
        VTooltipError,
    },

    mixins: [modalActions],

    data() {
        return {
            isButtonDisabled: true,
            email: '',
            message: '',
            files: [],
            feedback: 1,
            feedbackOptions: [
                {
                    id: 1,
                    text: 'Предложение',
                },
                {
                    id: 2,
                    text: 'Оставить отзыв',
                },
                {
                    id: 3,
                    text: 'Сообщить о баге',
                },
                {
                    id: 4,
                    text: 'Сотрудничество',
                },
            ],
        };
    },

    methods: {
        handeFileAdded(files) {
            this.files = files;
        },

        handleSubmitForm() {
            this.openModal('TheModalFeedback');
        },
    },
};
</script>

